
@import url("https://fonts.googleapis.com/css?family=Lato:400,900|Noto+Serif");
body {
  margin: 0em;
  padding: 0em;
}
.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}
@media only screen and (max-width: 768px) {
.page {
      margin-top: 4em;
}
}
.page .page-body {
    width: 65%;
    text-align: left;
    margin-bottom: 5em;
}
@media only screen and (max-width: 768px) {
.page .page-body {
        width: 100%;
}
}
.page .page-body ul {
      list-style: none;
      padding-left: 0em;
}
.page .page-body ul li {
        margin: 0em;
}
.page .page-body a {
      text-decoration: none;
}
.page .page-body a:hover {
        text-decoration: underline;
        color: #000;
}
.page .page-body a:visited {
        text-decoration: none;
        color: #000;
}
.content h2 {
  font-family: "Lato", sans-serif;
  font-weight: 900;
  font-size: 2.5rem;
  color: #3e70c1;
  margin: .4em 0em .1em 0em;
}
@media only screen and (max-width: 768px) {
.content h2 {
      font-size: 2.2rem;
}
}
.content h3 {
  font-family: "Lato", sans-serif;
  font-weight: 900;
  font-size: 1.5rem;
  color: #000;
  margin: .2em 0em;
}
.content h4 {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  color: #999;
  margin: 0em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h4 {
      font-size: 1.25rem;
}
}
.content h5 {
  font-family: "Lato", sans-serif;
  font-size: 1.35rem;
  margin: 1.2em 0em .6em 0em;
}
@media only screen and (max-width: 768px) {
.content h5 {
      font-size: 1.1rem;
}
}
.content p {
  white-space: pre-wrap;
  font-family: "Noto Serif", serif;
  font-size: 1.25rem;
  margin: .2em 0em;
}
@media only screen and (max-width: 768px) {
.content p {
      font-size: 1rem;
}
}
.header {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  background: #f5f5f5;
  border-radius: 5px;
  height: 200px;
  margin: 0em 2em 2em 2em;
}
@media only screen and (max-width: 768px) {
.header {
      height: 100px;
      margin: 1em;
}
}
.header .headshot {
    display: inline-block;
    height: 200px;
    border-radius: 5px 0px 0px 5px;
}
@media only screen and (max-width: 768px) {
.header .headshot {
        height: 100px;
}
}
.header .info {
    padding: 2em;
}
@media only screen and (max-width: 768px) {
.header .info {
        padding: .5em 1em .5em 1em;
}
}
.header .info h3 {
      margin: .2em 0em;
}
@media only screen and (max-width: 1024px) {
.header .info h3 {
          font-size: 1.2rem;
}
}
@media only screen and (max-width: 768px) {
.header .info h3 {
          font-size: 1rem;
}
}
.header .info p {
      margin: .1em 0em;
      font-size: 1.1rem;
}
@media only screen and (max-width: 1024px) {
.header .info p {
          font-size: .95rem;
}
}
@media only screen and (max-width: 768px) {
.header .info p {
          display: none;
}
}
.header .info .icon {
      height: 25px;
      margin: .5em .35em;
}
@media only screen and (max-width: 1024px) {
.header .info .icon {
          height: 20px;
}
}
.blogpost {
  padding: .5em 2em;
  border-radius: 5px;
}
@media only screen and (max-width: 768px) {
.blogpost {
      padding: .5em 1em;
}
}
.blogpost ul {
    margin: 0em;
    padding: 0em;
    list-style: none;
}



